*{
    margin: 0;
    padding: 0;
}

body{
    background-color: #0B2434;
    /* Centering the body */
    display:flex; 
    flex-direction:column; 
    justify-content:center;
    min-height:100vh;
}

.main-content{
    background-color: #F5F5F5;
    margin: 5% 25%;
    border-radius: 10px;
    padding: 2% 0;
}

.game-text{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.heading-text{
    font-family: 'Karla', sans-serif;
    color: #2B283A;
    font-size: 3rem;
}

.para-text{
    font-family: 'Inter', sans-serif;
    color: #4A4E74;
    font-size: 1.2rem;
    margin: 2% 5%;
    margin-bottom: 0;
}

.game-dice {
    display: grid;
    grid-template-columns: repeat(5,1fr);
    margin: 20px 2%;
}

.game-buttons {
    font-family: 'Karla', sans-serif;
    color: #2B283A;
    box-shadow: 5px 5px 5px rgb(0 0 0 / 15%);
    border-radius: 7px;
    font-size: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin: 10% 5%;
    padding: 8% 0;
}

.roll-dice{
    display: flex;
    justify-content: center;
    align-items: center;
}

.roll-dice-button{
    font-family: 'Karla', sans-serif;
    background-color: #5035FF;
    color: #FFFFFF;
    padding: 2% 6%;
    font-size: 2rem;
    border-radius: 7px;
    cursor: pointer;
    box-shadow: 5px 5px 5px rgb(0 0 0 / 15%);
    border: none;
}

.roll-dice-button:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.game-score{
    font-family: 'Inter', sans-serif;
    color: #4A4E74;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    margin: 12px 0;
}

.game-score > *{
    padding: 5px 0;
    text-align: center;
}

@media only screen and (max-width: 768px){
    .main-content{
        margin: 10%;
        padding: 4% 0;
    }
}